
.bottom-panel {
  position: absolute;
  bottom: 0;
  height: 80px;
  width: 100%;
  font-size: 0;
  border-top: 1px solid #000;
}
.bottom-panel .panel-content {
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.bottom-panel .panel-content .panel-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: inline-block;
  line-height: 80px;
  font-size: 16px;
}
.bottom-panel .panel-content .panel-item .active {
  color: #f00;
}


